body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  height: 100%;
  background-attachment: fixed;
}

#root{
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.brand{
  /* flex-grow: 1; */
  color:rgb(225, 216, 205);
  
}



.nav-btn{
  height: 6vh !important;
  width: 6vw !important;
  border: solid rgb(10, 186, 197) 2px !important;
  border-radius: 10px !important;
  background-color:rgb(225, 216, 205) !important;
  color: rgb(19, 100, 162) !important;
  font-size: smaller !important;
  font-weight: bold !important;
  margin-left: 10px !important;
}

.appBar{

  height: 8% !important;
}

.toolBar{
  background-color:rgb(17, 104, 158) !important;
  display: flex;
  justify-content: space-between;
  overflow: hidden;
}
.login-cont{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(maldives-1993704.jpg);
  background-repeat: inherit;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.login-form{
  border: 1px #007fba solid;
  padding: 40px;
  border-radius: 25px;
  background-color:rgba(225, 216, 205, 0.8);
  display: flex;
  flex-direction: column;
}

.login-inp{
  width: 300px;
}
.register-cont{
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  background-image: url(maldives-1993704.jpg);
  background-repeat: inherit;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}
.register-form{
  border: 1px #007fba solid;
  padding: 30px;
  border-radius: 25px;
  background-color:rgba(225, 216, 205, 0.8);
display: flex;
flex-direction: column;
}

.register-inp{
  width: 300px;
}

.span{
  padding: 10px;
  text-align: center;
}

.main{
  background-image: url(maldives-1993704.jpg);
  background-size: cover;
  display: flex;
  height: 92%;
  padding-left: 10px;
  padding-top: 60px;
  background-position: center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.vacations-cont{
  display: flex;
  flex-wrap: wrap;
  align-content: flex-start;
}

.vacation-paper{
  border: 1px #007fba solid;
  padding-top: 10px;
  width: 310px;
  height: 550px;
  margin: 10px;
  display: flex;
  align-items: center;
  flex-direction: column;
  text-align: center;
  background-color: rgba(225, 216, 205, 0.9)!important;
  border-radius: 19px!important;
  overflow: hidden;
}

.prop{
  margin: 13px;
}


.vacation-image{
  width: 100%;
  height: 35%;
}

.btns-cont{
  padding-left: 250px;
  
}

.vacConfig{
  height: 100vh;
  display: flex !important;
  justify-content: center !important;
  align-items: center !important;
  background-image: url(maldives-1993704.jpg);
  background-repeat: inherit;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.vacconfig-form{
  height: 70%;
  border: 1px #007fba solid;
  padding: 30px;
  border-radius: 25px;
  background-color:rgba(225, 216, 205, 0.9);
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-around;
}

.btn-cont{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.vacconfig-btn{
  margin: 5px !important;
}

.removevac{
  height: 100vh;
  display: flex;
  justify-content: center;
  text-align: center;
  align-items: center;
  background-image: url(maldives-1993704.jpg);
  background-repeat: inherit;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
}

.removevac-form{
  border: 1px #007fba solid;
  padding: 40px;
  border-radius: 25px;
  background-color:rgba(225, 216, 205, 0.9);
  display: flex;
  flex-direction: column;
}

.reports{
  padding-top: 8vh;
  background-image: url(maldives-1993704.jpg);
  background-attachment: fixed;
  height: 92vh;
  background-position: center;

}
.rep-form{
  background-color:rgba(225, 216, 205, 0.9);
  padding-top: 4px;
  height: 84.46vh;

}

